.Character {
    background-color: #5d6679;
    height: 500px;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: calc(10px + 2vmin);
    color: rgb(2, 15, 15);
}

button {
  position: relative;
  width: 100%;
  height: 60px;
  margin: 20px auto;
  padding: 0;
  font-size: 22px;
  text-align: center;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  overflow: hidden;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
  transition: transform 0.4s ease-in-out;
}

button:active {
  top: 1px;
  box-shadow: none;
}

button:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
  transition-duration: 0.4s;
  -webkit-transition-duration: 0.4s; /* Safari */
}

.icon {
  position: absolute;
  left: 20px;
}
.btn-green {
  background: #4c9cf8;
  border-bottom: 2px solid #4c9cf8;
  box-shadow: inset 0 -2px #4c9cf8;
}
.btn-green .icon {
  width: 24px;
  height: 24px;
  transition: all 0.4s ease-in-out;
}
.btn-green:hover .icon {
  transform: rotate(360deg) scale(1.2);
}